/* eslint jsx-a11y/anchor-is-valid: 0 */

import React from "react";
import {
  Container,
  Row,
  Col,
  Card,
  CardBody,
  Badge,
  Button,
} from "shards-react";

import PageTitle from "../components/common/PageTitle";
import axios from 'axios'

class BlogPosts extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      // Second list of posts.
      post: []
    };
  }

  componentDidMount() {
    axios.get('http://localhost:4000/api/v1/post')
      .then(response => {
        this.setState({ post: response.data.data });
      })
      .catch(error => {
        console.error(error);
      });
  }

  formatDate = (dateString) => {
    const date = new Date(dateString);
    const options = { weekday: 'short', year: 'numeric', month: 'short', day: 'numeric' };
    return date.toLocaleDateString('en-US', options);
  }

  getImage(url) {
    return `https://thehousechronicles.com/img/${url}`
  }
  

  render() {
    const {
      post,
    } = this.state;

    return (
      <Container fluid className="main-content-container px-4">
        {/* Page Header */}
        <Row noGutters className="page-header py-4">
          <PageTitle sm="4" title="Blog Posts" subtitle="Contents" className="text-sm-left" />
        </Row>

        {/* Second Row of Posts */}
        <Row>
          {post.map((post) => (
            <Col lg="6" sm="12" className="mb-4" key={post._id}>
              <Card small className="card-post card-post--aside card-post--1">
                <div
                  className="card-post__image"
                  style={{ backgroundImage: `url(${this.getImage(post.image)})` }}
                >
                  <Badge
                    pill
                    className={`card-post__category bg-dark`}
                  >
                    {post.category.name}
                  </Badge>
                  {/* <div className="card-post__author d-flex">
                    <a
                      href="#"
                      className="card-post__author-avatar card-post__author-avatar--small"
                      style={{ backgroundImage: `url('${post.authorAvatar}')` }}
                    >
                      Written by Anna Ken
                    </a>
                  </div> */}
                </div>
                <CardBody>
                  <h5 className="card-title">
                    <a className="text-fiord-blue" href="#">
                      {post.title}
                    </a>
                  </h5>
                  {/* <p className="card-text d-inline-block mb-3">{post.content}</p> */}
                  <span className="text-muted">{this.formatDate(post.createdAt)}</span>
                  <p className="text-muted">{post.author}</p>
                </CardBody>
              </Card>
              <Row className="ml-2 mt-2">
                <Button
                  onClick={() => {
                    sessionStorage.setItem("postId", post._id)
                    window.location.replace("/update-post")
                  }}
                >Edit</Button>
                <Button 
                style={{
                  backgroundColor: "red",
                  marginLeft: "4px",
                  border: "none"
                }}
                  onClick={() => {
                    if (window.confirm("Are you sure you want to delete this post?")) {
                      axios.delete(`https://thehousechronicles.com/api/v1/post/${post._id}`)
                        .then(response => {
                          alert(response.data.message);
                          window.location.reload();
                        })
                        .catch(error => {
                          console.error(error);
                        });
                    }
                  }}
                >Delete</Button>
              </Row>
            </Col>
          ))}
        </Row>
      </Container>
    );
  }
}

export default BlogPosts;
