/* eslint jsx-a11y/anchor-is-valid: 0 */

import React from "react";
import {
  Container,
  Row,
  Col,
  Card,
  CardBody,
  Badge,
  Button,
  Modal,
  ModalHeader,
  InputGroupAddon,
  FormCheckbox,
  ListGroup,
  ListGroupItem,
  ModalBody,
} from "shards-react";

import PageTitle from "../components/common/PageTitle";
import axios from 'axios'

class HomeSections extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      // Second list of posts.
      sections: [],
      open: false,
      categories: [],
      categoryname: "",
      categoryId: "",
      posts: [],
      addSection: false
    };
    this.toggle = this.toggle.bind(this);
  }

  componentDidMount() {
    axios.get('https://thehousechronicles.com/api/v1/home-section')
      .then(response => {
        this.setState({ sections: response.data });
      })
      .catch(error => {
        console.error(error);
      });

    axios.get('https://thehousechronicles.com/api/v1/category')
      .then(response => {
        this.setState({ categories: response.data.data });
      })
      .catch(error => {
        console.error(error);
      });

      this.fetchPostsByCategory()
  }

  componentDidUpdate(prevProps, prevState) {
    if (prevState.categoryname !== this.state.categoryname) {
      this.fetchPostsByCategory()
    }

    if (prevState.posts !== this.state.posts) {
      this.addSection()
    }
  }

  fetchPostsByCategory() {
    axios.get(`https://thehousechronicles.com/api/v1/filter-posts?category=${this.state.categoryname}`)
      .then(response => {
        const posts = response.data.data
        const postIds = posts.slice(0, 5).map(post => String(post._id))
        this.setState({ posts: postIds })
      })
      .catch(err => {
        console.log(err)
      })
  }

  addSection() {
    axios.post("https://thehousechronicles.com/api/v1/home-section", {
      category: this.state.categoryId,
      posts: this.state.posts
    })
    .then((response) => {
      alert(response.data.message)
      window.location.reload()
    })
    .catch((error) => {
      console.log(error)
    })
  }

  deleteSection(id) {
    axios.delete(`https://lmyx0j-4000.preview.csb.app/api/v1/home-section/${id}`)
      .then((response) => {
        alert(response.data.message)
        window.location.reload()
      })
      .catch((error) => {
        console.log(error)
      })
  }

  formatDate = (dateString) => {
    const date = new Date(dateString);
    const options = { weekday: 'short', year: 'numeric', month: 'short', day: 'numeric' };
    return date.toLocaleDateString('en-US', options);
  }

  getImage(url) {
    return `https://thehousechronicles.com/img/${url}`
  }

  toggle() {
    this.setState({
      open: !this.state.open
    });
  }

  render() {
    const {
      sections,
      open,
      categories,
    } = this.state;

    return (
      <Container fluid className="main-content-container px-4">
        {/* Page Header */}
        <Row noGutters className="page-header py-4">
          <Col lg="10">
            <PageTitle sm="4" title="Grid Banners" subtitle="Contents" className="text-sm-left" />
          </Col>
          <Col>
            <Button
              style={{
                background: "#000000",
                border: "none",
                marginTop: "4px"
              }}
              onClick={this.toggle}
            >
              Add Section
            </Button>
          </Col>
        </Row>

        {/* Second Row of Posts */}
        {
          sections.map((sec) => (
            <Col className="mt-2">
              <Col>
                <h4>
                  {sec.category.name}
                </h4>
              </Col>
              <Row key={sec._id}>
                {sec.posts.map((post) => (
                  <Col lg="6" sm="12" className="mb-4" key={post._id}>
                    <Card small className="card-post card-post--aside card-post--1">
                      <div
                        className="card-post__image"
                        style={{ backgroundImage: `url(${this.getImage(post.image)})` }}
                      >
                        <Badge
                          pill
                          className={`card-post__category bg-dark`}
                        >
                          {post.category.name}
                        </Badge>
                        {/* <div className="card-post__author d-flex">
                          <a
                            href="#"
                            className="card-post__author-avatar card-post__author-avatar--small"
                            style={{ backgroundImage: `url('${post.authorAvatar}')` }}
                          >
                            Written by Anna Ken
                          </a>
                        </div> */}
                      </div>
                      <CardBody>
                        <h5 className="card-title">
                          <a className="text-fiord-blue" href="#">
                            {post.title}
                          </a>
                        </h5>
                        {/* <p className="card-text d-inline-block mb-3">{post.content}</p> */}
                        <span className="text-muted">{this.formatDate(post.createdAt)}</span>
                        <p className="text-muted">{post.author}</p>
                      </CardBody>
                    </Card>
                  </Col>
                ))}
              </Row>
              <Button
                style={{
                  background: "red",
                  border: "none"
                }}
                onClick={() => this.deleteSection(sec._id)}
              >
                Delete {sec.category.name}
              </Button>
            </Col>
          ))
        }
        <Modal centered open={open} toggle={this.toggle}>
          <ModalHeader>
            <Row>
              <Col>
                <Button
                  style={{
                    background: "#000000",
                    border: "none"
                  }}
                  onClick={() => {
                    window.location.reload()
                  }}
                >
                  Refresh
                </Button>
              </Col>
            </Row>
          </ModalHeader>
          {
            categories.length > 0 ? (
              <ModalBody 
                style={{
                  height: "70vh",
                  overflowY: "scroll"
                }}
              >
                <ListGroup flush>
                  {categories.map(category => (
                    <ListGroupItem onClick={() => this.setState({ categoryId: category._id })} key={category._id} className="px-3 pb-2">
                      <FormCheckbox
                        className="mb-1"
                        value={category.name}
                        onChange={(event) => {
                          const selectedCategoryId = event.target.value;
                          this.setState({ open: false })
                          if (window.confirm(`You want to add ${category.name} section`)) {
                            this.setState({ 
                              categoryname: selectedCategoryId, 
                              addSection: true,
                            })
                          }
                        }}
                      >
                        {category.name}
                      </FormCheckbox>
                    </ListGroupItem>
                  ))}
                </ListGroup>
              </ModalBody>

            ) : null
          }
        </Modal>
      </Container>
    );
  }
}

export default HomeSections;
