/* eslint jsx-a11y/anchor-is-valid: 0 */

import React from "react";
import {
  Container,
  Row,
  Col,
  Card,
  CardBody,
  FormInput,
  Badge,
  Button,
  Modal, 
  ModalBody, 
  ModalHeader,
  ListGroup, 
  ListGroupItem,
  Form
} from "shards-react";

import PageTitle from "../components/common/PageTitle";
import axios from 'axios'

class Network extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      // Second list of posts.
      post: [],
      open: false,
      postIds: "",
      allPosts: [],
      link: "",
      active: false
    };
    this.toggle = this.toggle.bind(this);
  }

  componentDidMount() {
    axios.get('https://thehousechronicles.com/api/v1/interviews')
      .then(response => {
        const posts = response.data.data;
        this.setState({ post: posts }); // update the state
      })
      .catch(error => {
        console.error(error);
      });

      axios.get(`https://thehousechronicles.com/api/v1/post`)
      .then(response => {
        this.setState({ allPosts: response.data.data })
      })
      .catch(err => {
        console.log(err)
      })
  }

  addBanner(id, link) {
    axios.post(`https://thehousechronicles.com/api/v1/interviews/`, {posts: id, link: link})
      .then((response) => {
        alert(response.data.message)
        this.setState({ open: false, active: false })
        window.location.reload()
      })
      .catch((error) => {
        console.log(error)
      })
  }

  deleteBanner(id) {
    axios.delete(`https://thehousechronicles.com/api/v1/interviews/${id}`)
      .then((response) => {
        alert(response.data.message)
        window.location.reload()
      })
      .catch((error) => {
        console.log(error)
      })
  }



  toggle() {
    this.setState({
      open: !this.state.open
    });
  }

  formatDate = (dateString) => {
    const date = new Date(dateString);
    const options = { weekday: 'short', year: 'numeric', month: 'short', day: 'numeric' };
    return date.toLocaleDateString('en-US', options);
  }

  getImage(url) {
    return `https://thehousechronicles.com/img/${url}`
  }

  render() {
    const {
      post,
      open,
      allPosts,
      link,
      active,
      postIds
    } = this.state;

    return (
      <Container fluid className="main-content-container px-4">
        {/* Page Header */}
        <Row noGutters className="page-header py-4">
          <PageTitle sm="4" title="The House Chronicles Network" subtitle="Contents" className="text-sm-left" />
        </Row>

        {/* Second Row of Posts */}
        <Row>
          {post.map((post) => (
            <Col lg="6" sm="12" className="mb-4" key={post._id}>
              <Card small className="card-post card-post--aside card-post--1">
                <div
                  className="card-post__image"
                  style={{ backgroundImage: `url(${this.getImage(post.posts.image)})` }}
                >
                  <Badge
                    pill
                    className={`card-post__category bg-dark`}
                  >
                    {post.posts.category.name}
                  </Badge>
                  {/* <div className="card-post__author d-flex">
                    <a
                      href="#"
                      className="card-post__author-avatar card-post__author-avatar--small"
                      style={{ backgroundImage: `url('${post.authorAvatar}')` }}
                    >
                      Written by Anna Ken
                    </a>
                  </div> */}
                </div>
                <CardBody>
                  <h5 className="card-title">
                    <a className="text-fiord-blue" href="#">
                      {post.posts.title}
                    </a>
                  </h5>
                  {/* <p className="card-text d-inline-block mb-3">{post.content}</p> */}
                  <span className="text-muted">{this.formatDate(post.posts.createdAt)}</span>
                  <p className="text-muted">{post.posts.author}</p>
                </CardBody>
              </Card>
              <Button
                style={{
                  background: "red",
                  border: "none",
                  marginLeft: "2px",
                  marginTop: "4px"
                }}
                onClick={() => {
                  this.deleteBanner(post._id)
                }}
              >
                Delete
              </Button>
            </Col>
          ))}
        </Row>
        <Button 
          onClick={this.toggle}
          style={{
            background: "#000000",
            border: "none"
          }}
        >
          Add Banner
        </Button>
        <Modal centered open={open} toggle={this.toggle}>
          <ModalHeader>Update Slider Banner</ModalHeader>
          <ModalBody>
            <Row className="mx-auto">
              <Col lg="8">
                <FormInput
                  placeholder="input youtube link"
                  value={link}
                  onChange={event => this.setState({ link: event.target.value })}
                />
              </Col>
              <Col lg="4">
                <Button
                  style={{
                    background: "#000000",
                    border: "none"
                  }}
                  onClick={() => {
                    this.addBanner(postIds, link)
                  }}
                >
                  create
                </Button>
              </Col>
            </Row>
            <Col>
              <ListGroup 
                flush={false}
                className="mt-2"
                tag="ul"
              >
                {
                  allPosts.map((post) => (
                    <ListGroupItem 
                      key={post._id}
                      style={{
                        fontSize: "small"
                      }}
                      disabled={active}
                      tag="li"
                      onClick={() => {
                        this.setState({ postIds: post._id})
                        alert("post added, now input the youtube link and click create")
                        this.setState({ active: true })
                      }}
                    >
                        {post.title}
                    </ListGroupItem>
                  ))
                }
              </ListGroup>
            </Col>
          </ModalBody>
        </Modal>
      </Container>
    );
  }
}

export default Network;
