import React, { Component } from "react";
import { Container, Row, Col } from "shards-react";

import PageTitle from "../components/common/PageTitle";
import Editor from "../components/add-new-post/Editor";
import SidebarActions from "../components/add-new-post/SidebarActions";
import SidebarCategories from "../components/add-new-post/SidebarCategories";
import axios from "axios"

class UpdatePost extends Component {
  constructor(props) {
    super(props);
    this.state = {
      title: "",
      author: "",
      file: null,
      content: "",
      category: ""
    };
  }

  handleTitleChange = (event) => {
    this.setState({ title: event });
  };

  handleAuthorChange = (event) => {
    this.setState({ author: event });
  };

  handleFileChange = (file) => {
    this.setState({ file: file });
  };

  handleContentChange = (content) => {
    this.setState({ content: content });
  };

  handleCategoryChange = (event) => {
    this.setState({ category: event })
  }

  uploadPost = () => {
    const formData = new FormData();
    formData.append("title", this.state.title);
    formData.append("author", this.state.author)
    formData.append("image", this.state.file)
    formData.append("content", this.state.content)
    formData.append("category", this.state.category)

    const postId = sessionStorage.getItem("postId")

    axios.put(`https://thehousechronicles.com/api/v1/post/${postId}`, {
        title: this.state.title,
        author: this.state.author,
        image: this.state.file,
        content: this.state.content,
        category: this.state.category
    })
      .then((response) => {
        alert(response.data.message)
        window.location.replace("/blog-posts")
      })
      .catch((error) => {
        console.log(error)
      })
  }

  componentDidMount() {
    const postId = sessionStorage.getItem("postId")
    axios.get(`https://thehousechronicles.com/api/v1/post/${postId}`)
    .then((response) => {
        this.setState({ title: response.data.data.title })
        this.setState({ author: response.data.data.author })
        this.setState({ file: response.data.data.image })
        this.setState({ content: response.data.data.content })
        this.setState({ category: response.data.data.category })
    })
  }

  render() {
    return (
      <Container fluid className="main-content-container px-4 pb-4">
        {/* Page Header */}
        <Row noGutters className="page-header py-4">
          <PageTitle sm="4" title="Add New Post" subtitle="Blog Posts" className="text-sm-left" />
        </Row>

        <Row>
          {/* Editor */}
          <Col lg="9" md="12">
            <Editor
              title={this.state.title}
              author={this.state.author}
              file={this.state.file}
              content={this.state.content}
              onTitleChange={this.handleTitleChange}
              onAuthorChange={this.handleAuthorChange}
              onFileChange={this.handleFileChange}
              onContentChange={this.handleContentChange}
            />
          </Col>

          {/* Sidebar Widgets */}
          <Col lg="3" md="12">
            <SidebarActions 
              setUploadPost={this.uploadPost}
            />
            <SidebarCategories 
              onCategoryChange={this.handleCategoryChange}
            />
          </Col>
        </Row>
      </Container>
    );
  }
}

export default UpdatePost;
