import React, { Component } from "react";
import PropTypes from "prop-types";
import {
  Card,
  CardHeader,
  CardBody,
  ListGroup,
  ListGroupItem,
  Button,
  InputGroup,
  InputGroupAddon,
  FormCheckbox,
  FormInput
} from "shards-react";
import axios from 'axios'

class SidebarCategories extends Component {
  constructor(props) {
    super(props);
    this.state = {
      categories: []
    };
  }

  componentDidMount() {
    axios.get('https://lmyx0j-4000.preview.csb.app/api/v1/category')
      .then(response => {
        this.setState({ categories: response.data.data });
      })
      .catch(error => {
        console.error(error);
      });
  }

  render() {
    const { title } = this.props;
    const { categories } = this.state;

    return (
      <Card small className="mb-3">
        <CardHeader className="border-bottom">
          <h6 className="m-0">{title}</h6>
          <Button
            onClick={() => {
              window.location.reload()
            }}
            style={{
              background: "#000000",
              border: "none"
            }}
          >
            Refresh
          </Button>
        </CardHeader>
        <CardBody className="p-0">
          <ListGroup flush>
            {categories.map(category => (
              <ListGroupItem key={category._id} className="px-3 pb-2">
                <FormCheckbox
                  className="mb-1"
                  value={category._id}
                  onChange={(event) => {
                    const selectedCategoryId = event.target.value;
                    this.props.onCategoryChange(selectedCategoryId);

                    const filteredCategory = categories.filter(
                      (fct) => fct._id === selectedCategoryId
                    );
                    this.setState({ categories: filteredCategory });
                  }}
                >
                  {category.name}
                </FormCheckbox>
              </ListGroupItem>
            ))}
          </ListGroup>
        </CardBody>
      </Card>
    );
  }
}

SidebarCategories.propTypes = {
  /**
   * The component's title.
   */
  title: PropTypes.string
};

SidebarCategories.defaultProps = {
  title: "Categories"
};

export default SidebarCategories;
