import React, { Component } from "react";
import PropTypes from "prop-types";
import {
  Card,
  CardHeader,
  CardBody,
  ListGroup,
  ListGroupItem,
  Button
} from "shards-react";

class SidebarActions extends Component {
  render() {
    const { title, setUploadPost } = this.props;

    return (
      <Card small className="mb-3">
        <CardHeader className="border-bottom">
          <h6 className="m-0">{title}</h6>
        </CardHeader>

        <CardBody className="p-0">
          <ListGroup flush>
            <ListGroupItem className="d-flex px-3 border-0">
              <Button onClick={setUploadPost} theme="accent" size="sm">
                <i className="material-icons">file_copy</i> Publish
              </Button>
              {/* <Button outline theme="accent" size="sm">
                <i className="material-icons">save</i> Save Draft
              </Button> */}
            </ListGroupItem>
          </ListGroup>
        </CardBody>
      </Card>
    );
  }
}

SidebarActions.propTypes = {
  /**
   * The component's title.
   */
  title: PropTypes.string,
  /**
   * The function to call when the "Publish" button is clicked.
   */
  setUploadPost: PropTypes.func
};

SidebarActions.defaultProps = {
  title: "Actions"
};

export default SidebarActions;
