import React, { Component } from 'react';
import PropTypes from 'prop-types';
import {
  Card,
  CardHeader,
  CardBody,
  ListGroup,
  ListGroupItem,
  CardFooter,
  Row,
  Col,
  FormSelect
} from 'shards-react';
import axios from "axios"

class TopReferrals extends Component {
  constructor(props) {
    super(props)

    this.state = {
      post: []
    }
  }

  componentDidMount() {
    axios.get('https://thehousechronicles.com/api/v1/post')
      .then(response => {
        this.setState({ post: response.data.data.slice(0, 5) }); // only pass first 5 items
      })
      .catch(error => {
        console.error(error);
      });
  }

  formatDate = (dateString) => {
    const date = new Date(dateString);
    const options = { weekday: 'short', year: 'numeric', month: 'short', day: 'numeric' };
    return date.toLocaleDateString('en-US', options);
  }
  
  render() {
    const { post } = this.state;
    return (
      <Card small>
        <CardHeader className="border-bottom">
          <h6 className="m-0">Newly Added</h6>
          <div className="block-handle" />
        </CardHeader>

        <CardBody className="p-0">
          <ListGroup small flush className="list-group-small">
            {post.map((item, idx) => (
              <ListGroupItem key={idx} className="d-flex px-3">
                <span className="text-semibold text-fiord-blue">{item.title}</span>
                <span className="ml-auto text-right text-semibold text-reagent-gray">
                  {this.formatDate(item.createdAt)}
                </span>
              </ListGroupItem>
            ))}
          </ListGroup>
        </CardBody>
      </Card>
    );
  }
}

export default TopReferrals;
