import React, { Component } from "react";
import ReactQuill from "react-quill";
import { Card, CardBody, Form, FormInput } from "shards-react";
import CustomFileUpload from "../components-overview/CustomFileUpload";
import axios from "axios"

import "react-quill/dist/quill.snow.css";
import "../../assets/quill.css";

class Editor extends Component {
  constructor(props) {
    super(props);

    this.state = {
      title: props.title || "",
      author: props.author || "",
      file: props.file || "Upload an image",
      content: props.content || "",
    };
  }

  handleFileChange = (newFile) => {
      const formData = new FormData();
      formData.append("file", newFile);

      axios.post("https://thehousechronicles.com/api/v1/upload", formData)
        .then((res) => {
          console.log(res.data)
          this.setState({ file: res.data })
          this.props.onFileChange(res.data)
        })
        .catch((err) => {
          console.log(err)
        })
  };

  render() {
    return (
      <Card small className="mb-3">
        <CardBody>
          <Form className="add-new-post">
            <FormInput
              size="lg"
              className="mb-3"
              placeholder="Your Post Title"
              value={this.props.title}
              onChange={(event) => this.props.onTitleChange(event.target.value)}
            />
            <FormInput
              size="lg"
              className="mb-3"
              placeholder="Author Name"
              value={this.props.author}
              onChange={(event) => this.props.onAuthorChange(event.target.value)}
            />
            <CustomFileUpload file={this.state.file} onFileChange={this.handleFileChange} />
            <ReactQuill
              className="add-new-post__editor mb-1"
              value={this.props.content}
              onChange={(value) => this.props.onContentChange(value)}
            />
          </Form>
        </CardBody>
      </Card>
    );
  }
}

export default Editor;
